import React, { useState } from 'react'
import ProtocolForm from "./ProtocolForm";
import PageHeader from "../../ap_components/PageHeader";
import PeopleOutlineTwoToneIcon from '@material-ui/icons/PeopleOutlineTwoTone';
import { Paper, makeStyles, TableBody, TableRow, TableCell, Toolbar, InputAdornment, Typography } from '@material-ui/core';
import useTable from "../../ap_components/useTable";
import * as employeeService from "../../services/employeeService";
import Controls from "../../ap_components/controls/Controls";
import { Search } from "@material-ui/icons";
import AddIcon from '@material-ui/icons/Add';
import Popup from "../../ap_components/Popup";
import EditOutlinedIcon from '@material-ui/icons/EditOutlined';
import CloseIcon from '@material-ui/icons/Close';
import Notification from "../../ap_components/Notification";
import ConfirmDialog from "../../ap_components/ConfirmDialog";

const useStyles = makeStyles(theme => ({
    pageContent: {
        margin: theme.spacing(5),
        padding: theme.spacing(3)
    },
    searchInput: {
        width: '75%'
    },
    newButton: {
        position: 'absolute',
        right: '10px'
    }
}))

const headCells = [
    { id: 'id', label: 'ID' },
    { id: 'chain_id', label: 'Chain ID' },
    { id: 'name', label: 'Name' },
    { id: 'logo_url', label: 'Logo URL' },
    { id: 'site_url', label: 'Site URL' },
    { id: 'type', label: 'Type' },
    { id: 'lauched_date', label: 'Launch Date' },
    { id: 'is_hacked', label: 'Hacked' },
    // { id: 'audited', label: 'Audited' },
    // { id: 'audit_report_url', label: 'Audit Report URL' },
    // { id: 'auto_compounding', label: 'Auto Compounding' },
    // { id: 'github_contract', label: 'Github Contract' },
    // { id: 'social_media_urls', label: 'Social Media URLs' },
    // { id: 'big_name_urls', label: 'Big Name URLs' },
    { id: 'actions', label: 'Actions', disableSorting: true }
]


export default function Protocols() {

    const classes = useStyles();
    const [recordForEdit, setRecordForEdit] = useState(null)
    const [records, setRecords] = useState(employeeService.getAllEmployees())
    const [filterFn, setFilterFn] = useState({ fn: items => { return items; } })
    const [openPopup, setOpenPopup] = useState(false)
    const [notify, setNotify] = useState({ isOpen: false, message: '', type: '' })
    const [confirmDialog, setConfirmDialog] = useState({ isOpen: false, title: '', subTitle: '' })

    const {
        TblContainer,
        TblHead,
        TblPagination,
        recordsAfterPagingAndSorting
    } = useTable(records, headCells, filterFn);

    const handleSearch = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value == "")
                    return items;
                else
                    return items.filter(x => x.fullName.toLowerCase().includes(target.value))
            }
        })
    }

    const addOrEdit = (employee, resetForm) => {
        // if (employee.id == 0)
        //     employeeService.insertEmployee(employee)
        // else
        //     employeeService.updateEmployee(employee)
        // resetForm()
        // setRecordForEdit(null)
        // setOpenPopup(false)
        // setRecords(employeeService.getAllEmployees())
        // setNotify({
        //     isOpen: true,
        //     message: 'Submitted Successfully',
        //     type: 'success'
        // })
    }

    const openInPopup = item => {
        setRecordForEdit(item)
        setOpenPopup(true)
    }

    const onDelete = id => {
        setConfirmDialog({
            ...confirmDialog,
            isOpen: false
        })
        employeeService.deleteEmployee(id);
        setRecords(employeeService.getAllEmployees())
        setNotify({
            isOpen: true,
            message: 'Deleted Successfully',
            type: 'error'
        })
    }

    return (
        <>
            {/* <PageHeader
                title="New Employee"
                subTitle="Form design with validation"
                icon={<PeopleOutlineTwoToneIcon fontSize="large" />}
            /> */}
            <Paper className={classes.pageContent}>

                <Toolbar>
                    <Controls.Input
                        label="Search Protocols"
                        className={classes.searchInput}
                        InputProps={{
                            startAdornment: (<InputAdornment position="start">
                                <Search />
                            </InputAdornment>)
                        }}
                        onChange={handleSearch}
                    />
                    <Typography>Del/Edit on Actions Col</Typography>
                    <Controls.Button
                        text="Add New"
                        variant="outlined"
                        startIcon={<AddIcon />}
                        className={classes.newButton}
                        onClick={() => { setOpenPopup(true); setRecordForEdit(null); }}
                    />
                </Toolbar>
                <TblContainer>
                    <TblHead />
                    <TableBody>
                        {
                            recordsAfterPagingAndSorting().map(item =>
                            (

                                <TableRow key={item.id}>
                                    <TableCell>{item.chain_id}</TableCell>
                                    <TableCell>{item.name}</TableCell>
                                    <TableCell>{item.logo_url}</TableCell>
                                    <TableCell>{item.site_url}</TableCell>
                                    <TableCell>{item.type}</TableCell>
                                    <TableCell>{item.lauched_date}</TableCell>
                                    <TableCell>{item.is_hacked}</TableCell>
                                    <TableCell>{item.audited}</TableCell>
                                    {/* <TableCell>{item.audit_report_url}</TableCell>
                                    <TableCell>{item.auto_compounding}</TableCell>
                                    <TableCell>{item.github_contract}</TableCell>
                                    <TableCell>{item.social_media_urls}</TableCell>
                                    <TableCell>{item.big_name_urls}</TableCell> */}
                                    <TableCell>
                                        <Controls.ActionButton
                                            color="primary"
                                            onClick={() => { openInPopup(item) }}>
                                            <EditOutlinedIcon fontSize="small" />
                                        </Controls.ActionButton>
                                        <Controls.ActionButton
                                            color="secondary"
                                            onClick={() => {
                                                setConfirmDialog({
                                                    isOpen: true,
                                                    title: 'Are you sure to delete this record?',
                                                    subTitle: "You can't undo this operation",
                                                    onConfirm: () => { onDelete(item.id) }
                                                })
                                            }}>
                                            <CloseIcon fontSize="small" />
                                        </Controls.ActionButton>
                                    </TableCell>
                                </TableRow>)
                            )
                        }
                    </TableBody>
                </TblContainer>
                <TblPagination />
            </Paper>
            <Popup
                title="Protocol Form, here is for a trader admin to maintain protocol info, only for the purpose of data model confirmation."
                openPopup={openPopup}
                setOpenPopup={setOpenPopup}
            >
                <ProtocolForm
                    recordForEdit={recordForEdit}
                    addOrEdit={addOrEdit} />
            </Popup>
            <Notification
                notify={notify}
                setNotify={setNotify}
            />
            <ConfirmDialog
                confirmDialog={confirmDialog}
                setConfirmDialog={setConfirmDialog}
            />
        </>
    )
}
