import React, { useState } from "react";
import { Grid, Typography, Button } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import MUIDataTable from "mui-datatables";
import { getChain, getPools } from "../../services/poolService";
import { getProtocols } from "../../services/protocolService";
import Controls from "../../ap_components/controls/Controls";
import PageTitle from "../../components/PageTitle";
import DialogRiskEvaluate from "../../components/ap_risk_evaluate";
import ImgNetworkFee from "../../images/network_fee.png";
import ImgTrendByVolumn from "../../images/trend_by_volumn.png";
// import Button from "../../ap_components/controls/Button";

const datatableData = [
  ["stETH Curve", 100, 1, 2, 3, 4, 0.1, 0.2],
  ["3Pool Curve", 100, 1, 2, 3, 4, 0.1, 0.2],
  ["MIM Curve", 100, 1, 2, 3, 4, 0.1, 0.2],
];

const useStyles = makeStyles(theme => ({
  tableOverflow: {
    overflow: 'auto'
  }
}))

export default function Tables() {

  const classes = useStyles();
  let [dataProtocol, setDataProtocol] = useState(getProtocols('1'));
  let [dataPool, setPoolData] = useState(getPools('Ethereum/SushiSwap'));
  let [chainId, setChainId] = useState('1');
  let [chainName, setChainName] = useState('Ethereum');
  let [protocol, setProtocol] = useState('Protocol');
  let [openRiskEvaluateDialog, setOpenRiskEvaluateDialog] = useState(false);

  const handleInputChange = (e) => {
    const { name, value } = e.target
    setChainId(value);

    let chains = getChain();
    for (var i = 0; i < chains.length; i++) {
      if (chains[i].id == value) {
        setChainName(chains[i].title);
        break;
      }
    }
    setDataProtocol(getProtocols(value));
  }

  const onRowClick = (rowData, rowMeta) => {
    console.log('rowData============', rowData);
    let { dataIndex, rowIndex } = rowMeta;
    let key = chainName + "/" + rowData[0];
    console.log('key============', key);
    setPoolData(getPools(key));
  }

  const onEvaluateRisk = (value) => {
    console.log('value===', value.rowData[0]);
    let key = chainName + "/" + value.rowData[0];
    setProtocol(key);
    setOpenRiskEvaluateDialog(true);
  }

  const  handleCloseRiskEvaluateDialog = () =>{
    setOpenRiskEvaluateDialog(false);
  }

  return (
    <>
      <Grid container spacing={3}>
        <Grid item xs={10}>
          <Controls.Select
            name="chain_id"
            label="Chain Name"
            value={chainId}
            onChange={handleInputChange}
            options={getChain()}
          // error={errors.departmentId}
          />
          {/* <PageTitle title="Tables" /> */}
          <Grid container spacing={4}>
            <Grid item xs={12}>
              <MUIDataTable
                title="Protocol List"
                data={dataProtocol}
                columns={["Protocol Name", {
                  name: "Risk",
                  options: {
                    filter: false,
                    customBodyRender: (value, tableMeta, updateValue) => (
                      <Button
                        variant="outlined"
                        onClick={event => onEvaluateRisk(tableMeta)}
                      >Evaluate</Button>
                    )
                  }
                }]}
                options={{
                  filterType: "checkbox",
                  onRowClick: onRowClick
                }}
              />
            </Grid>
          </Grid>
          <Grid container spacing={4}>
            <Grid item xs={12}>
              <MUIDataTable
                title="Pool List"
                data={dataPool}
                columns={["Name", "Liquidity", "TVL 24H", "TVL 7D", "TVL 30D", "TVL 3M", "APR", "APY"]}
                options={{
                  filterType: "checkbox",
                }}
              />
            </Grid>
          </Grid>
        </Grid>

        <Grid item xs={2}>
          <img src={ImgTrendByVolumn} alt="AP" width="250" style={{ marginTop: '20px' }} />
          <img src={ImgNetworkFee} alt="AP" width="250" style={{ marginTop: '20px' }} />
        </Grid>
      </Grid>
      <DialogRiskEvaluate open = {openRiskEvaluateDialog} handleClose={handleCloseRiskEvaluateDialog} protocol={protocol}/>
    </>
  );
}
