import React, { useState, useEffect } from 'react'
import { Grid, } from '@material-ui/core';
import Controls from "../../ap_components/controls/Controls";
import { useForm, Form } from '../../ap_components/useForm';


const initialFValues = {
    id: '',
    community_id: '',
    logo_url: '',
    native_token_id: '',
    wrapped_token_id: '',
    site_url: '',
    has_multisig_wallet: false,
    consensus_protocol: '',
    launch_date: new Date(),
}

export default function BlockchainForm(props) {
    const { addOrEdit, recordForEdit } = props

    const validate = (fieldValues = values) => {
        let temp = { ...errors }
        // if ('fullName' in fieldValues)
        //     temp.fullName = fieldValues.fullName ? "" : "This field is required."
        // if ('email' in fieldValues)
        //     temp.email = (/$^|.+@.+..+/).test(fieldValues.email) ? "" : "Email is not valid."
        // if ('mobile' in fieldValues)
        //     temp.mobile = fieldValues.mobile.length > 9 ? "" : "Minimum 10 numbers required."
        // if ('departmentId' in fieldValues)
        //     temp.departmentId = fieldValues.departmentId.length != 0 ? "" : "This field is required."
        setErrors({
            ...temp
        })

        if (fieldValues == values)
            return Object.values(temp).every(x => x == "")
    }

    const {
        values,
        setValues,
        errors,
        setErrors,
        handleInputChange,
        resetForm
    } = useForm(initialFValues, true, validate);

    const handleSubmit = e => {
        e.preventDefault()
        if (validate()) {
            addOrEdit(values, resetForm);
        }
    }

    useEffect(() => {
        if (recordForEdit != null)
            setValues({
                ...recordForEdit
            })
    }, [recordForEdit])

    return (
        <Form onSubmit={handleSubmit}>
            <Grid container>
                <Grid item xs={6}>
                    <Controls.Input
                        name="id"
                        label="ID"
                        value={values.id}
                        onChange={handleInputChange}
                        // error={errors.fullName}
                    />
                    <Controls.Input
                        name="community_id"
                        label="Community ID"
                        value={values.community_id}
                        onChange={handleInputChange}
                        // error={errors.email}
                    />
                    <Controls.Input
                        name="logo_url"
                        label="Logo URL"
                        value={values.logo_url}
                        onChange={handleInputChange}
                        // error={errors.mobile}
                    />
                    <Controls.Input
                        name="native_token_id"
                        label="Native Token ID"
                        value={values.native_token_id}
                        onChange={handleInputChange}
                    />

                </Grid>
                <Grid item xs={6}>
                    <Controls.Input
                        name="wrapped_token_id"
                        label="Wrapped Token ID"
                        value={values.wrapped_token_id}
                        onChange={handleInputChange}
                        // options={employeeService.getDepartmentCollection()}
                        // error={errors.departmentId}
                    />
                    <Controls.Input
                        name="site_url"
                        label="Site URL"
                        value={values.site_url}
                        onChange={handleInputChange}
                    />
                    <Controls.Checkbox
                        name="has_multisig_wallet"
                        label="Multisig Wallet"
                        value={values.has_multisig_wallet}
                        onChange={handleInputChange}
                    />
                    <Controls.Input
                        name="consensus_protocol"
                        label="Consensus Protocol"
                        value={values.consensus_protocol}
                        onChange={handleInputChange}
                    />
                    
                    <Controls.DatePicker
                        name="launch_date"
                        label="Launch Date"
                        value={values.launch_date}
                        onChange={handleInputChange}
                    />

                    <div>
                        <Controls.Button
                            type="submit"
                            text="Submit" />
                        <Controls.Button
                            text="Reset"
                            color="default"
                            onClick={resetForm} />
                    </div>
                </Grid>
            </Grid>
        </Form>
    )
}

