import React, { useState, useEffect } from 'react'
import { Grid, } from '@material-ui/core';
import Controls from "../../ap_components/controls/Controls";
import { useForm, Form } from '../../ap_components/useForm';
import * as employeeService from "../../services/employeeService";


const genderItems = [
    { id: 'male', title: 'Male' },
    { id: 'female', title: 'Female' },
    { id: 'other', title: 'Other' },
]

/**
 * Id, 
 * chain_id,
 *  name, 
 * logo_url, 
 * site_url, 
 * has_supported_portfolio(bool), 
 * portfolio_item_list, 
 * type(dex/yield aggregator/lending), 
 * lauched_date, 
 * is_hacked, 
 * audited(bool), 
 * audit_report_url, 
 * auto_compounding(bool), 
 * github_contract, 
 * social_media_urls, 
 * whitepaper_url, 
 * big_name_urls, 
 */
const initialFValues = {
    id: '',
    chain_id: '',
    name: '',
    logo_url: '',
    site_url: '',
    type: '',
    lauched_date: new Date(),
    is_hacked: false,
    audited: true,
    audit_report_url: '',
    auto_compounding: false,
    github_contract: '',
    social_media_urls: '',
    big_name_urls: '',
}

export default function ProtocolForm(props) {
    const { addOrEdit, recordForEdit } = props

    const validate = (fieldValues = values) => {
        let temp = { ...errors }
        // if ('fullName' in fieldValues)
        //     temp.fullName = fieldValues.fullName ? "" : "This field is required."
        // if ('email' in fieldValues)
        //     temp.email = (/$^|.+@.+..+/).test(fieldValues.email) ? "" : "Email is not valid."
        // if ('mobile' in fieldValues)
        //     temp.mobile = fieldValues.mobile.length > 9 ? "" : "Minimum 10 numbers required."
        // if ('departmentId' in fieldValues)
        //     temp.departmentId = fieldValues.departmentId.length != 0 ? "" : "This field is required."
        setErrors({
            ...temp
        })

        if (fieldValues == values)
            return Object.values(temp).every(x => x == "")
    }

    const {
        values,
        setValues,
        errors,
        setErrors,
        handleInputChange,
        resetForm
    } = useForm(initialFValues, true, validate);

    const handleSubmit = e => {
        e.preventDefault()
        if (validate()) {
            addOrEdit(values, resetForm);
        }
    }

    useEffect(() => {
        if (recordForEdit != null)
            setValues({
                ...recordForEdit
            })
    }, [recordForEdit])

    return (
        <Form onSubmit={handleSubmit}>
            <Grid container>
            {/* const initialFValues = {
                    id: '',
                    chain_id: '',
                    name: '',
                    logo_url: '',
                    site_url: '',
                    type: '',
                    lauched_date: new Date(),
                    is_hacked: false,
                    audited: true,
                    audit_report_url: '',
                    auto_compounding: false,
                    github_contract: '',
                    social_media_urls: '',
                    big_name_urls: '',
} */}
                <Grid item xs={6}>
                    <Controls.Input
                        name="id"
                        label="ID"
                        value={values.id}
                        onChange={handleInputChange}
                        // error={errors.fullName}
                    />
                    <Controls.Input
                        name="chain_id"
                        label="Chain ID"
                        value={values.chain_id}
                        onChange={handleInputChange}
                        // error={errors.email}
                    />
                    <Controls.Input
                        name="name"
                        label="Name"
                        value={values.name}
                        onChange={handleInputChange}
                        // error={errors.mobile}
                    />
                    <Controls.Input
                        name="logo_url"
                        label="Logo URL"
                        value={values.logo_url}
                        onChange={handleInputChange}
                    />

                </Grid>
                <Grid item xs={6}>
                    <Controls.Input
                        name="site_url"
                        label="Site URL"
                        value={values.site_url}
                        onChange={handleInputChange}
                        // options={employeeService.getDepartmentCollection()}
                        // error={errors.departmentId}
                    />
                    <Controls.Input
                        name="type"
                        label="Type"
                        value={values.type}
                        onChange={handleInputChange}
                    />
                    <Controls.DatePicker
                        name="lauched_date"
                        label="Launch Date"
                        value={values.lauched_date}
                        onChange={handleInputChange}
                    />
                    <Controls.Checkbox
                        name="is_hacked"
                        label="Hacked"
                        value={values.is_hacked}
                        onChange={handleInputChange}
                    />
                </Grid>
                <Grid item xs={6}>
                    <Controls.Checkbox
                        name="audited"
                        label="Audited"
                        value={values.audited}
                        onChange={handleInputChange}
                    />
                    <Controls.Input
                        name="audit_report_url"
                        label="Audit Report URL"
                        value={values.audit_report_url}
                        onChange={handleInputChange}
                    />
                    
                    <Controls.Checkbox
                        name="auto_compounding"
                        label="Auto-compounding"
                        value={values.auto_compounding}
                        onChange={handleInputChange}
                    />
                    <Controls.Input
                        name="github_contract"
                        label="Github Contract"
                        value={values.github_contract}
                        onChange={handleInputChange}
                    />
                </Grid>
                <Grid item xs={6}>
                    <Controls.Input
                        name="social_media_urls"
                        label="Social Media URLs"
                        value={values.social_media_urls}
                        onChange={handleInputChange}
                    />
                    <Controls.Input
                        name="big_name_urls"
                        label="Big Name URLs"
                        value={values.big_name_urls}
                        onChange={handleInputChange}
                    />
                    
                    <div>
                        <Controls.Button
                            type="submit"
                            text="Submit" />
                        <Controls.Button
                            text="Reset"
                            color="default"
                            onClick={resetForm} />
                    </div>
                </Grid>
            </Grid>
        </Form>
    )
}

